import React from 'react';
import { useDispatch } from 'react-redux';
import { clearAuth } from '@/store/slice/authSlice';
import Dropdown from 'react-bootstrap/Dropdown';

import '@/assets/scss/member/dropdown.scss';

const MemberDropDown = () => {
  const dispatch = useDispatch();

  const handleSignOut = () => {
    dispatch(clearAuth());
  };

  return (
    <Dropdown>
      <Dropdown.Toggle variant="primary"></Dropdown.Toggle>

      <Dropdown.Menu>
        <Dropdown.Item href="#/member"></Dropdown.Item>
        <Dropdown.Item href="#/member/purchaseRecord"></Dropdown.Item>
        <Dropdown.Item href="#/member/bonusRecord"></Dropdown.Item>
        <Dropdown.Item onClick={handleSignOut}></Dropdown.Item>
      </Dropdown.Menu>
    </Dropdown>
  );
};

export default MemberDropDown;
